import React, { useState } from 'react';
import {
  Box,
  Card,
  CardContent,
  CardMedia,
  Typography,
  Container,
  Button,
  Chip,
  TextField,
  InputAdornment,
  IconButton,
  Rating,
  Fab,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from '@mui/material';
import {
  Search as SearchIcon,
  Add as AddIcon,
  Favorite as FavoriteIcon,
  Share as ShareIcon,
  Edit as EditIcon,
  Category as CategoryIcon,
} from '@mui/icons-material';

interface Catalog {
  id: number;
  title: string;
  description: string;
  category: string;
  image?: string;
  rating: number;
  price: number;
  inStock: boolean;
}

const Catalogs: React.FC = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [openDialog, setOpenDialog] = useState(false);

  // Mock data
  const [catalogs] = useState<Catalog[]>([
    {
      id: 1,
      title: 'Premium Headphones',
      description: 'High-quality wireless headphones with noise cancellation',
      category: 'Electronics',
      rating: 4.5,
      price: 299.99,
      inStock: true,
    },
    {
      id: 2,
      title: 'Smart Watch',
      description: 'Advanced smartwatch with health monitoring features',
      category: 'Electronics',
      rating: 4.2,
      price: 199.99,
      inStock: true,
    },
    {
      id: 3,
      title: 'Coffee Maker',
      description: 'Automatic coffee machine with multiple brewing options',
      category: 'Home & Kitchen',
      rating: 4.7,
      price: 149.99,
      inStock: false,
    },
    {
      id: 4,
      title: 'Running Shoes',
      description: 'Professional running shoes with advanced cushioning',
      category: 'Sports',
      rating: 4.3,
      price: 89.99,
      inStock: true,
    },
  ]);

  const categories = ['all', 'Electronics', 'Home & Kitchen', 'Sports', 'Books', 'Clothing'];

  const filteredCatalogs = catalogs.filter(catalog => {
    const matchesSearch = catalog.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         catalog.description.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesCategory = selectedCategory === 'all' || catalog.category === selectedCategory;
    return matchesSearch && matchesCategory;
  });

  return (
    <Container maxWidth="lg">
      <Box sx={{ mb: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom>
          Product Catalog
        </Typography>
        <Typography variant="body1" color="text.secondary">
          Browse and manage your product inventory
        </Typography>
      </Box>

      {/* Search and Filter */}
      <Box sx={{ display: 'flex', gap: 2, mb: 4, flexWrap: 'wrap' }}>
        <TextField
          placeholder="Search products..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SearchIcon />
              </InputAdornment>
            ),
          }}
          sx={{ flexGrow: 1, minWidth: 300 }}
        />
        <FormControl sx={{ minWidth: 200 }}>
          <InputLabel>Category</InputLabel>
          <Select
            value={selectedCategory}
            onChange={(e) => setSelectedCategory(e.target.value)}
            label="Category"
          >
            {categories.map((category) => (
              <MenuItem key={category} value={category}>
                {category === 'all' ? 'All Categories' : category}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
        <Button
          variant="contained"
          startIcon={<AddIcon />}
          onClick={() => setOpenDialog(true)}
        >
          Add Product
        </Button>
      </Box>

      {/* Product Grid */}
      <Box 
        sx={{ 
          display: 'grid', 
          gridTemplateColumns: 'repeat(auto-fill, minmax(300px, 1fr))', 
          gap: 3,
          mb: 4
        }}
      >
        {filteredCatalogs.map((catalog) => (
          <Card key={catalog.id} sx={{ maxWidth: 345, height: '100%', display: 'flex', flexDirection: 'column' }}>
            <CardMedia
              sx={{ height: 200, bgcolor: 'grey.200', display: 'flex', alignItems: 'center', justifyContent: 'center' }}
            >
              <CategoryIcon sx={{ fontSize: 60, color: 'grey.400' }} />
            </CardMedia>
            <CardContent sx={{ flexGrow: 1 }}>
              <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', mb: 1 }}>
                <Typography gutterBottom variant="h6" component="div">
                  {catalog.title}
                </Typography>
                <Chip 
                  label={catalog.inStock ? 'In Stock' : 'Out of Stock'} 
                  color={catalog.inStock ? 'success' : 'error'}
                  size="small"
                />
              </Box>
              
              <Typography variant="body2" color="text.secondary" paragraph>
                {catalog.description}
              </Typography>
              
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                <Rating value={catalog.rating} precision={0.1} readOnly size="small" />
                <Typography variant="body2" color="text.secondary" sx={{ ml: 1 }}>
                  ({catalog.rating})
                </Typography>
              </Box>
              
              <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                <Typography variant="h6" color="primary">
                  ${catalog.price}
                </Typography>
                <Chip label={catalog.category} variant="outlined" size="small" />
              </Box>
            </CardContent>
            
            <Box sx={{ display: 'flex', justifyContent: 'space-between', p: 1 }}>
              <IconButton aria-label="add to favorites">
                <FavoriteIcon />
              </IconButton>
              <IconButton aria-label="edit">
                <EditIcon />
              </IconButton>
              <IconButton aria-label="share">
                <ShareIcon />
              </IconButton>
            </Box>
          </Card>
        ))}
      </Box>

      {filteredCatalogs.length === 0 && (
        <Box sx={{ textAlign: 'center', py: 8 }}>
          <Typography variant="h6" color="text.secondary">
            No products found
          </Typography>
          <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
            Try adjusting your search criteria or add a new product
          </Typography>
          <Button variant="contained" onClick={() => setOpenDialog(true)}>
            Add First Product
          </Button>
        </Box>
      )}

      {/* Add Product Dialog */}
      <Dialog open={openDialog} onClose={() => setOpenDialog(false)} maxWidth="sm" fullWidth>
        <DialogTitle>Add New Product</DialogTitle>
        <DialogContent>
          <Box sx={{ pt: 2 }}>
            <TextField
              fullWidth
              label="Product Title"
              sx={{ mb: 2 }}
            />
            <TextField
              fullWidth
              label="Description"
              multiline
              rows={3}
              sx={{ mb: 2 }}
            />
            <TextField
              fullWidth
              label="Price"
              type="number"
              InputProps={{ startAdornment: <InputAdornment position="start">$</InputAdornment> }}
              sx={{ mb: 2 }}
            />
            <FormControl fullWidth sx={{ mb: 2 }}>
              <InputLabel>Category</InputLabel>
              <Select label="Category">
                {categories.filter(cat => cat !== 'all').map((category) => (
                  <MenuItem key={category} value={category}>
                    {category}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpenDialog(false)}>Cancel</Button>
          <Button variant="contained">Add Product</Button>
        </DialogActions>
      </Dialog>

      {/* Floating Action Button */}
      <Fab
        color="primary"
        aria-label="add"
        sx={{ position: 'fixed', bottom: 16, right: 16 }}
        onClick={() => setOpenDialog(true)}
      >
        <AddIcon />
      </Fab>
    </Container>
  );
};

export default Catalogs;